	click (event) {
		let clicks_queue = this.state.clicks_queue;
		let x = event.clientX, y = event.clientY;
		if(!clicks_queue.hasOwnProperty('to_send')){
			clicks_queue.to_send = random.int(2,7);
		}
		clicks_queue.history.push(Date.now());
		clicks_queue.coords.push({ x, y });
		clicks_queue.count = clicks_queue.count + 1;
		if(clicks_queue.count >= clicks_queue.to_send || clicks_queue.count >= clicks_queue.all){
			clicks_queue.to_send = clicks_queue.count + random.int(2,5);
			socket.json.send({'method': 'click', 'game_id': this.state.game.id, clicks: clicks_queue});
			if(clicks_queue.count >= clicks_queue.all){
				this.setState({ popout: <ScreenSpinner/> });
			}
		}

		this.setState({ clicks_queue });

		if(this.state.is_admin){
			try{
				let click_icon = document.createElement('img');
				click_icon.className = 'click_icon';
				click_icon.src = 'https://i.ibb.co/Tbbz4m0/2.png';
				click_icon.style.left = x + 'px';
				click_icon.style.top = y + 'px';
				document.getElementById('panel_game_inner').appendChild(click_icon);
				setTimeout(()=>{
					click_icon.remove();
				}, 150);
			}catch (e) {
				console.error(e);
			}
		}
	}
	click_marathon () {
		socket.json.send({ method: 'clickMarathon' });
		if(document.getElementById('ore_icon').className !== 'ore_animate'){
			document.getElementById('ore_icon').className = 'ore_animate';
			document.getElementById('ore_shapes').className = 'ore_shapes '+this.state.marathon_ore+'_shapes';
			setTimeout(()=>{
				document.getElementById('ore_icon').className = 'null';
				document.getElementById('ore_shapes').className = 'null';
			}, 300);
		}
	}
	click_boss (e) {
		let clicks_queue = this.state.clicks_queue_boss;
		if(!clicks_queue.hasOwnProperty('to_send')){
			clicks_queue.to_send = random.int(2,5);
		}
		clicks_queue.count = clicks_queue.count + 1;
		try{
			if(osname === ANDROID && clicks_queue.count % 150 === 0){
				bridge.send("VKWebAppShowNativeAds", {ad_format:"preloader"} );
			}
		}catch (e) {}

		clicks_queue.history.push(Date.now());
		if(clicks_queue.count >= clicks_queue.to_send || clicks_queue.count >= this.state.boss_hp){
			clicks_queue.to_send = clicks_queue.count + random.int(2,10);
			socket.json.send({'method': 'clickBoss', clicks: clicks_queue});
		}

		this.setState({ clicks_queue_boss: clicks_queue });

		this.personClick(e);
	}
