		let skins = this.state.persons[this.state.person].skins.map((val, number) =>
			<Person persons={this.state.persons} person={this.state.person} skin={number}/>
		);
		this.setState({ skins_panel: skins, skins_i: this.state.user.activeSkins[this.state.person] });
		this.go('person_info');
