								<div>
									<Input style={{ marginTop: '8px' }} type="text" defaultValue={this.state.api_token} readOnly={true}/>
									<Button style={{ marginTop: '8px' }} size={'xl'} onClick={()=>{ bridge.send("VKWebAppCopyText", {text: this.state.api_token}); }}>Скопировать</Button>
								</div>
