import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {SimpleCell, Avatar, Group, Card, Separator, Footer, Link} from '@vkontakte/vkui';
import '../css/GameInfo.css';

export class GameInfo extends PureComponent {

    render() {
        return (
            <Group>
                <SimpleCell disabled
                            indicator={<span style={{ fontSize: '14px' }}>{(this.props.game.endTime - this.props.game.startTime) + ' мс'}</span>}
                            description={this.props.game.clicks + ' кликов'}>
                    <span style={{ fontSize: '14px' }}>
                        { 'ID: ' + this.props.game.id }
                    </span>
                </SimpleCell>
                <SimpleCell multiline style={{ background: this.props.game.winner === this.props.game.player1 ? 'rgba(75, 179, 75, .2)' : 'rgba(240, 92, 68, .2)' }}
                    before={<Avatar size={32} src={this.props.game.photo1} />}
                            href={'https://vk.com/id' + this.props.game.player1}
                            target='_blank' indicator={this.props.game.kps1 + ' кпс'}
                            description={this.props.game.clicks1 + ' кликов'}>
                    <span style={{ color: this.props.game.winner === this.props.game.player1 ? 'rgb(75, 179, 75)' : 'rgb(240, 92, 68)' }}>
                        { this.props.game.name1 + (this.props.game.banned1 ? ' [ЗАБАНЕН]' : '') + (this.props.game.exited === this.props.game.player1 ? ' [ВЫШЕЛ]' : '') }
                    </span>
                </SimpleCell>
                <SimpleCell multiline style={{ background: this.props.game.winner === this.props.game.player2 ? 'rgba(75, 179, 75, .2)' : 'rgba(240, 92, 68, .2)' }}
                    before={<Avatar size={32} src={this.props.game.photo2} />}
                            href={'https://vk.com/id' + this.props.game.player2}
                            target='_blank' indicator={this.props.game.kps2 + ' кпс'}
                            description={this.props.game.clicks2 + ' кликов'}>
                    <span style={{ color: this.props.game.winner === this.props.game.player2 ? 'rgb(75, 179, 75)' : 'rgb(240, 92, 68)' }}>
                        { this.props.game.name2 + (this.props.game.banned2 ? ' [ЗАБАНЕН]' : '') + (this.props.game.exited === this.props.game.player2 ? ' [ВЫШЕЛ]' : '') }
                    </span>
                </SimpleCell>
            </Group>
        )
    }

}

export class GameStat extends PureComponent {

    render() {
        let scheme;
        try{
            scheme  = document.body.attributes.getNamedItem('scheme').value;
        }catch (e) {
            scheme = 'bright_light';
        }

        return (
            <Card size={'l'} className={'game_info_card'}>
                <div style={{ margin: 24 }}>
                    <span style={{ position: 'absolute', fontWeight: 700, fontSize: this.props.game.winner == this.props.user_id ? '22px' : '18px', opacity: .6, textAlign: 'center', width: '100%', left: 0, color: this.props.game.winner == this.props.user_id ? 'rgb(75, 179, 75)' : 'rgb(240, 92, 68)', top: -12 }}>
                        {
                            this.props.game.winner == this.props.user_id ?
                                'ПОБЕДА'
                                :
                                'ПОРАЖЕНИЕ'
                        }
                    </span>
                    <div style={{ display: 'flex', justifyContent: 'space-between', paddingBottom: 16 }}>
                        <Link href={'https://vk.com/id' + this.props.game.player1} target='_blank'>
                            <div style={{ margin: '0 16px', width: 72, color: scheme === 'bright_light' ? 'black' : 'white' }}>
                                <Avatar shadow={false} size={72} src={this.props.game.photo1}/>
                                <span style={{ wordSpacing: 72, display: 'flex', alignItems: 'center', textAlign: 'center', marginTop: 4, fontSize: 14, opacity: .7, fontWeight: 500 }}>{this.props.game.name1}</span>
                            </div>
                        </Link>
                        <Link href={'https://vk.com/id' + this.props.game.player2} target='_blank'>
                            <div style={{ margin: '0 16px', width: 72, color: scheme === 'bright_light' ? 'black' : 'white' }}>
                                <Avatar shadow={false} size={72} src={this.props.game.photo2}/>
                                <span style={{ wordSpacing: 72, display: 'flex', alignItems: 'center', textAlign: 'center', marginTop: 4, fontSize: 14, opacity: .7, fontWeight: 500 }}>{this.props.game.name2}</span>
                            </div>
                        </Link>
                    </div>

                    <Separator wide style={{ paddingBottom: 10 }}/>

                    <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: 6 }}>
                        <span style={{ fontWeight: 500 }}>{this.props.game.clicks1 + ' кликов'}</span>
                        <span style={{ fontWeight: 500 }}>{this.props.game.clicks2 + ' кликов'}</span>
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: 6 }}>
                        <span style={{ fontWeight: 500 }}>{this.props.game.kps1 + ' кпс'}</span>
                        <span style={{ fontWeight: 500 }}>{this.props.game.kps2 + ' кпс'}</span>
                    </div>

                    <Separator wide style={{ padding: '12px 0' }}/>

                    <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: 6 }}>
                        <span style={{ fontWeight: 500 }}>Время игры:</span>
                        <span>{(this.props.game.endTime - this.props.game.startTime) + ' мс'}</span>
                    </div>

                    <Footer>{ 'ID: ' + this.props.game.id }</Footer>
                </div>
            </Card>
        )
    }

}

