import React, { PureComponent } from 'react';
import '../css/Person.css';
import '../css/App.css';
import land from '../img/platform.png';
import ice from '../img/ice.png';
const random = require('random');
let interval;
export class Person extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            pers : null
        }
    }

    componentDidMount() {
        try{
            let interval_realized = false;
            let user = this.props.user;
            let persons = this.props.persons;
            let i = this.props.person;
            let skin = this.props.hasOwnProperty('skin') ?
                persons[i].skins[this.props.skin].url
                :
                user.activeSkins.hasOwnProperty(i+'') ?
                    user.activeSkins[i] === null ?
                        persons[i].skins[0].url
                        :
                        persons[i].skins[user.activeSkins[i]].url
                    :
                    persons[i].skins[0].url

            if(this.props.is_game){
                let used = false;
                let transform = random.int(-40, 40);
                interval = setInterval(()=>{
                    let chance = random.float(0,1);
                    if(chance<=0.25){
                        if(i === 1){
                            transform = -transform;
                            this.setState({ pers:
                                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                        <img src={skin} onClick={this.props.onClick} style={{ height: '170px', transform: 'translate('+transform+'%)' }}/>
                                    </div>
                            });
                        }else if (i === 2){
                            let pi = random.int(1,2);
                            if(!used){
                                used = true;
                                if(pi===1){
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} style={{ height: '110px' }}/>
                                                <img src={skin} onClick={this.props.onClick} style={{ marginLeft: "25px", height: '110px' }}/>
                                            </div>
                                    });
                                }else{
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} onClick={this.props.onClick} style={{ marginLeft: "25px", height: '110px' }}/>
                                                <img src={skin} style={{ height: '110px' }}/>
                                            </div>
                                    });
                                }
                            }else{
                                used = false;
                                this.setState({ pers:
                                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                            <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                        </div>
                                });
                            }
                        }else if (i === 3){
                            if(!used){
                                used = true;
                                this.setState({ pers:
                                        <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                            <img className={'hr jump'} src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                        </div>
                                });
                                setTimeout(()=>{
                                    used = false;
                                    this.setState({ pers:
                                            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                                            </div>
                                    });
                                }, 1250);
                            }
                        }
                    }

                    if (i === 4){
                        if(!interval_realized){
                            clearInterval(interval);
                            interval = setInterval(()=>{
                                let clicks = random.int(50,120),
                                    clicks1 = random.int(50,120),
                                    clicks2 = random.int(50,120);
                                try{
                                    if(random.boolean()) document.getElementById('game_clicks_all').innerText = clicks;
                                    if(random.boolean()) document.getElementById('game_clicks1').innerText = clicks1;
                                    if(random.boolean()) document.getElementById('game_clicks2').innerText = clicks2;

                                    if(random.boolean()) document.getElementById('game_platform').src = random.boolean() ? land : ice;
                                }catch (e) {  }
                            }, 100);
                        }
                    }

                    interval_realized = true;
                },400);
            }
            this.setState({ pers: <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                    <img src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                </div> });
        }catch (e) {

        }
    }

    render() {
        try{
            if(!this.props.is_game){
                clearInterval(interval);
            }
        }catch (e) {}
        return (
            this.state.pers
        )
    }

}