import React, { PureComponent } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import {Banner, Avatar, Tappable, Link} from '@vkontakte/vkui';
import '../css/PromoBannerCustom.css';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';

export class PromoBannerCustom extends PureComponent {

    constructor (props){
        super(props);

        this.state = {
            show: true
        }
    }

    render() {
        return (
            this.state.show &&
            <div id='promoCustom' style={{ position: 'relative' }}>
                <Link href={this.props.bannerData.trackingLink}>
                    <Banner
                        before={<Avatar mode='image' src={this.props.bannerData.iconLink}/>}
                        header={this.props.bannerData.title}
                        asideMode='expand'
                    />
                </Link>
                <div className='closeButton' onClick={() => this.setState({ show: false })}>
                    <Tappable className='circle'>
                        <Avatar size={22}><Icon24Cancel width={18} height={18} fill='rgb(153, 162, 173);'/></Avatar>
                    </Tappable>
                </div>
            </div>
        )
    }

}